<template>
	<view class="su-section">
		发大厦的发但是ccc
		<!-- <view class="su-contain">
			<button type="primary" plain="true" @click="initPrinter">初始化打印机</button>
		</view> -->
		<scroll-view class="scrool-list" scroll-y="true" v-if="deviceList && deviceList.length > 0">
			<view v-for="(item, index) in deviceList" :key="index + 3" class="list-item">
				<view class="items">
					<text class="text">{{ item.name }}</text>
					<text class="text" style="padding-left: 20rpx">{{ item.address }}</text>
				</view>
				<view class="items">
					<text class="text text2" @click="pairBluetooth()">连接</text>
				</view>
			</view>
		</scroll-view>
		<!-- <view class="su-contain" v-if="startPrinter">
			<button type="primary" plain="true" @click="printText">打印文本</button>
		</view> -->
		<view class="su-contain" >
			<button type="primary" plain="true" @click="printTextDemo">测试一</button>
		</view>
		<view class="su-contain" >
			<button type="primary" plain="true" @click="printTextDemo">测试二</button>
		</view>
		<!-- <view class="su-contain">
			<button type="primary" plain="true" @click="getPrinterStatus">获取打印机状态</button>
		</view> -->
	</view>
</template>

<script>
// #ifdef APP-PLUS
let iminPrinter = uni.requireNativePlugin('Hd-iminPrinter');
// #endif
export default {
	data() {
		return {
			printerType: 'Bluetooth',
			deviceList: [],
			startPrinter: false,
			it:{},
		};
	},
	onLoad() {
		// #ifdef APP-PLUS
		iminPrinter.initReceiver((res) => {
			this.initPrinter();
		});
		// #endif
	},
	methods: {
		initPrinter() {
			iminPrinter.InitPrinter(this.printerType, (res) => {
				if (res.code === 200) {
					this.deviceList = res['data'];
					//let item = res.data.filter(r=>{
					//	return r.name == 'BluetoothPrinter'
					//})
					//console.log(item);
					//console.log("11111111111");
					//this.pairBluetooth(item)
					console.log("111111111111111")
					for(var i=0;i<this.deviceList.length;i++){
						if(this.deviceList[i].name == "BluetoothPrinter"){
							this.it = this.deviceList[i];
						}
					}
					this.pairBluetooth();
					//console.log(res['data'],`iminPrinter---`, JSON.stringify(res));
				}
			});
		},
		pairBluetooth() {
			iminPrinter.pairBluetooth(this.it, (res) => {
				if (res.code === 200) {
					this.startPrinter = true;
				} else {
					uni.showModal({
						title: '温馨提示',
						content: JSON.stringify(res),
						success: function (res) {
							if (res.confirm) {
								console.log('用户点击确定');
							}
						}
					});
				}
			});
		},
		printText() {
			iminPrinter.setAlignment(1);
			iminPrinter.setTextSize(25);
			iminPrinter.printText('iMin致力于使用先进的技术来帮助合作伙伴实现业务数字化。我们致力于成为东盟国家领先的智能商务设备提供商，帮助合作伙伴有效地连接...');
			iminPrinter.printAndFeedPaper(200);
		},
		printTextDemo() {
			try{
				iminPrinter.printTextDemo();
			}catch(e){
				//TODO handle the exception
				console.log(e)
			}
		},
		printTextDemo() {
			try{
				iminPrinter.printTextDemo2();
			}catch(e){
				//TODO handle the exception
				console.log(e)
			}
		},
		getPrinterStatus() {
			iminPrinter.getPrinterStatus((res) => {
				uni.showModal({
					title: '温馨提示',
					content: JSON.stringify(res),
					success: function (res) {
						if (res.confirm) {
							console.log('用户点击确定');
						}
					}
				});
			});
		}
	}
};
</script>

<style lang="scss"></style>
